"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

interface Photo {
  id: string
  image: string
  age: number
  timestamp: number
}

export default function AdminDashboard() {
  const [photos, setPhotos] = useState<Photo[]>([])
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(true)
  const router = useRouter()

  useEffect(() => {
    const adminToken = localStorage.getItem("adminToken")
    if (!adminToken) {
      router.push("/admin/login")
      return
    }

    fetchPhotos()
  }, [router])

  const fetchPhotos = async () => {
    try {
      const response = await fetch("/api/photos", {
        headers: {
          Authorization: localStorage.getItem("adminToken") || "",
        },
      })

      if (!response.ok) {
        throw new Error("Erreur lors du chargement des photos")
      }

      const data = await response.json()
      setPhotos(data.photos)
    } catch (err) {
      setError("Impossible de charger les photos")
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm("Êtes-vous sûr de vouloir supprimer cette photo ?")) {
      return
    }

    try {
      const response = await fetch(`/api/photos?id=${id}`, {
        method: "DELETE",
        headers: {
          Authorization: localStorage.getItem("adminToken") || "",
        },
      })

      if (!response.ok) {
        throw new Error("Erreur lors de la suppression")
      }

      setPhotos(photos.filter(photo => photo.id !== id))
    } catch (err) {
      setError("Impossible de supprimer la photo")
    }
  }

  const handleLogout = () => {
    localStorage.removeItem("adminToken")
    router.push("/admin/login")
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-900">
        <p className="text-white">Chargement...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-900 p-6">
      <Card className="max-w-7xl mx-auto p-6 bg-white/10 backdrop-blur-lg">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-2xl font-bold text-white">
            Photos soumises
          </h1>
          <Button
            variant="destructive"
            onClick={handleLogout}
          >
            Déconnexion
          </Button>
        </div>

        {error && (
          <div className="bg-red-500/20 text-red-200 p-4 rounded-lg mb-6">
            {error}
          </div>
        )}

        {photos.length === 0 ? (
          <p className="text-gray-400 text-center py-8">
            Aucune photo n&apos;a été soumise pour le moment.
          </p>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {photos.map((photo) => (
              <Card key={photo.id} className="bg-white/5 p-4">
                <div className="aspect-video relative mb-4">
                  <img
                    src={photo.image}
                    alt={`Soumission #${photo.id}`}
                    className="w-full h-full object-cover rounded-lg"
                  />
                </div>
                <div className="space-y-2">
                  <p className="text-gray-200">
                    Âge détecté: {photo.age} ans
                  </p>
                  <p className="text-gray-400 text-sm">
                    Date: {new Date(photo.timestamp).toLocaleString()}
                  </p>
                  <div className="flex gap-2">
                    <a
                      href={photo.image}
                      download={`photo-${photo.id}.jpg`}
                      className="flex-1"
                    >
                      <Button variant="outline" className="w-full">
                        Télécharger
                      </Button>
                    </a>
                    <Button
                      variant="destructive"
                      onClick={() => handleDelete(photo.id)}
                      className="flex-1"
                    >
                      Supprimer
                    </Button>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        )}
      </Card>
    </div>
  )
}
